	}
}

/** @section Waves */
.waves {
	position: absolute;
	top: 0;
	bottom: 0;
	left: 0;
	right: 0;
	z-index: 2;
	width: 100% !important;
	height: 100% !important;
	-webkit-transform-origin: 40% 50%;
	        transform-origin: 40% 50%;
	-webkit-transform: rotate(-9deg) scale(1.03);
	        transform: rotate(-9deg) scale(1.03);
	pointer-events: none;
}

